.fitting__input-label {
  font-size: 13px;
  font-family: "Georgia", "Arial", sans-serif;
  font-style: italic;
  line-height: 1.385;
  margin-bottom: 11px; }

.fitting__input {
  margin-bottom: 28px;
  width: 100%;
  height: 38px;
  border: 2px solid #1f1f1f;
  padding-left: 28px; }

.fitting__input::placeholder {
  font-size: 14px;
  color: #b98348;
  line-height: 2.714;
  text-align: left; }

.fitting__input::-webkit-inner-spin-button {
  display: none; }

.fitting__input::-webkit-calendar-picker-indicator {
  opacity: 0; }

.fitting__btn {
  cursor: pointer;
  width: 100%;
  border: none;
  border-bottom: 3px solid #8b5c28;
  background-color: #b98348;
  color: #ffffff;
  padding: 15px;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  transition: 0.3s; }

.fitting__btn:hover {
  opacity: 0.8; }

@media (max-width: 1024px) {
