  .fitting__wrapper-side {
    flex-direction: column;
    margin-left: auto;
    margin-right: auto; }
  .fitting__input-wrapper {
    width: 100%; } }

.footer {
  background-color: #f6f6f6;
  border-top: 1px solid #ababab;
  border-bottom: 1px solid #ababab; }

.footer__wrapper {
  display: flex;
  justify-content: space-between; }

.footer__nav {
  display: flex;
  border-right: 1px solid #ababab; }

.footer__nav,
.footer__contacts {
  padding: 50px 20px; }

.footer__list {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-right: 65px; }

.footer__nav-item {
  margin-bottom: 12px; }

.footer__nav-link {
  font-size: 15px;
  font-family: "ProximaNovaCondenced", "Arial", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  transition: 0.3s; }

.footer__nav-link:hover {
  opacity: 0.7; }

.footer__list--main .footer__nav-link {
  color: #b98348; }

.footer__list--catalog .footer__nav-link {
  color: #1f1f1f;
  border-bottom: 1px solid #c2c2c2; }

.footer__contacts {
  font-size: 14px; }

.footer__contacts-title {
  text-transform: uppercase;
  font-weight: 600;
  line-height: 2.143;
  margin: 0; }

.footer__contacts-address,
.footer__contacts-tel {
